.bg-bubbles{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	z-index: 1;
	
	li{
		position: absolute;
		list-style: none;
		display: block;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: rgba(111,160,224,.25);
		// background-color: rgba(233, 244, 246,1);
        bottom: -160px;
		
		-webkit-animation: square 25s infinite;
		animation:         square 25s infinite;
		
		-webkit-transition-timing-function: linear;
		transition-timing-function: linear;
		
		&:nth-child(1){
			left: 10%;
		}
		
		&:nth-child(2){
			left: 20%;
			
			width: 80px;
			height: 80px;
			
			animation-delay: 2s;
			animation-duration: 17s;
		}
		
		&:nth-child(3){
			left: 25%;
			animation-delay: 4s;
		}
		
		&:nth-child(4){
			left: 40%;
			width: 60px;
			height: 60px;
			
			animation-duration: 22s;
			
			background-color: fade(white, 25%);
		}
		
		&:nth-child(5){
			left: 70%;
		}
		
		&:nth-child(6){
			left: 80%;
			width: 120px;
			height: 120px;
			
			animation-delay: 3s;
			background-color: fade(white, 20%);
		}
		
		&:nth-child(7){
			left: 32%;
			width: 160px;
			height: 160px;
			
			animation-delay: 7s;
		}
		
		&:nth-child(8){
			left: 55%;
			width: 20px;
			height: 20px;
			
			animation-delay: 15s;
			animation-duration: 40s;
		}
		
		&:nth-child(9){
			left: 25%;
			width: 10px;
			height: 10px;
			
			animation-delay: 2s;
			animation-duration: 40s;
			background-color: fade(white, 30%);
		}
		
		&:nth-child(10){
			left: 90%;
			width: 160px;
			height: 160px;
			animation-delay: 11s;
			animation-duration: 35s;
			background-color:transparent;
		}
		&:nth-child(11){
			left: 10%;
			width: 100px;
			height: 100px;
			animation-delay: 1s;
			animation-duration: 15s;
			background-color:transparent;
			border: 1px solid #eee;
			border-radius: 50% 40% / 50% 50%;
		}
		&:nth-child(12){
			left: 25%;
			width: 50px;
			height: 50px;
			animation-delay: 3s;
			animation-duration: 20s;
			background-color:transparent;
			border: 1px solid #eee;
			border-radius: 50% 40% / 50% 50%;
		}
		&:nth-child(13){
			left: 75%;
			width: 90px;
			height: 90px;
			animation-delay: 5s;
			animation-duration: 25s;
			background-color:transparent;
			border: 1px solid #eee;
			border-radius: 50% 40% / 50% 50%;
		}
		&:nth-child(14){
			left: 55%;
			width: 140px;
			height: 140px;
			animation-delay: 10s;
			animation-duration: 10s;
			background-color:transparent;
			border: 1px solid #eee;
			border-radius: 50% 40% / 50% 50%;
		}
	}
}
@media (max-width:576px){
	// .bg-bubbles{
	// 	display:none;
	// }
}

@-webkit-keyframes square {
    0%   { transform: translateY(0); }
    100% { transform: translateY(-700px) rotate(600deg); }
}
@keyframes square {
    0%   { transform: translateY(0); }
    100% { transform: translateY(-700px) rotate(600deg); }
}


